import React from 'react';


const About = () => {
  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-8">
          <div className="card">
            <div className="card-header text-center">
              <h1>About Us</h1>
            </div>
            <div className="card-body">
              <p>
                Coforge Technologies is a global digital services and solutions provider that leverages emerging technologies and deep domain expertise to deliver real-world business impact for its clients. Founded in 1992 and headquartered in Noida, India, Coforge has a strong presence worldwide with over 24,000 employees.
              </p>
              <p>
                The company specializes in digital supply chain, artificial intelligence, cloud infrastructure, data management, and enterprise applications. Coforge is committed to transforming businesses at the intersect of domain expertise and emerging technologies.
              </p>
              <p>
                Coforge has been recognized as a leader in various sectors and continues to innovate and deliver exceptional value to its clients.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
